onmessage = function (event) {
  function Uint8ArrayToString(fileData) {
    let dataString = "";
    for (let i = 0; i < fileData.length; i++) {
      dataString += String.fromCharCode(fileData[i]);
    }

    return dataString;
  }
  let str = Uint8ArrayToString(event.data);
  postMessage(str);
};
